const SOURCE_VECTOR = 'vector';

export default class ThemeWrapper {
    static TYPE = {
        URL: 'URL',
        OBJECT: 'OBJECT',
        RESOLVED_SCHEMA: 'RESOLVED_SCHEMA'
    };

    constructor(type, options) {
        this.options = options;
        this.type = type;

        this.getSource = this.getSource.bind(this);
    }

    unwrap() {
        return this.options;
    }

    getType() {
        return this.type;
    }

    getSource() {
        return this.getType() === (ThemeWrapper.TYPE.URL || ThemeWrapper.TYPE.OBJECT) ?
            SOURCE_VECTOR :
            this.options.source;
    }
}



